@font-face {
  font-family: 'old-standard-regular';
  src: url('./fonts/OldStandardTT-Regular.ttf')
}

@font-face {
  font-family: 'old-standard-bold';
  src: url('./fonts/OldStandardTT-Bold.ttf')
}

@font-face {
  font-family: 'old-standard-italic';
  src: url('./fonts/OldStandardTT-Italic.ttf')
}

html, body {
  background-color: #021b39;
}
.header {
  font-family: 'old-standard-regular';
  display: flex;
  visibility: visible;
  opacity: 1;
  background: linear-gradient(#ffc3ca,#a8a9e7);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: start;
  // margin : 5em 50% 5em 80px;
  .about-me-container {
    margin: 4em;
    width: 50%;
    h1 {
      font-family: 'old-standard-bold';
      font-size: 34px;
    }
    h2 {
      font-family: 'old-standard-regular';
      font-size: 32px;
    }
    h3 {
      font-family: 'old-standard-italic';
      font-size: 18px;
      font-style: italic;
    }
  }
  .my-stack-container{
    margin: 4em;
    width: 50%;
    color: #fbe9eb;
    display:grid;
    gap: 10px;
    grid-template-columns: repeat(3, 125px);
    grid-template-rows: repeat(4, 125px);
    place-content: center;
    text-align: center;
    .my-stack {
      align-self: center;
      border: 1px solid #fbe9eb4d;
      padding: 10px 0;
      border-radius: 15px;
      .stack-name {
        margin: 5px;
      }
    }
    .my-stack:hover {
      transform: scale(2);
      transition: all 200ms;
    }
  }
}

.footer {
  font-family: 'old-standard-regular';
  display: block;
  visibility: visible;
  opacity: 1;
  background: linear-gradient(#ffc3ca,#a8a9e7);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
  margin: 50px 10px 50px 10px;
  ul {
    font-weight: 300;
    list-style: none;
    border-top: 10px;
    padding: 0;
    .email {
      font-weight: 900;
    }
    .logo-container {
      display: flex;
      place-content: center;
      margin: 15px;
      li {
        margin: 15px;
        svg {
          color: #ffc3ca
        }
      }
      li:hover {
        transform: scale(1.30);
        transition: all 1200ms;
      }
    }
  }
}
.project-list {
  font-family: 'old-standard-bold';
  margin: 0 80px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  place-content: center;
  gap: 60px;
  .project-link{
    text-decoration: none;
  }
  figure{
    margin : 0;
    border-radius: 15px;
    background-color: #FFF;
    box-shadow: 7px 7px 5px #373737;
  }
  figure:hover img {
    opacity: 1;
    -webkit-animation: flash 1.5s;
    animation: flash 1.5s;
  }
  @-webkit-keyframes flash {
    0% {
      opacity: .4;
    }
    100% {
      opacity: 1;
    }
  }
  @keyframes flash {
    0% {
      opacity: .4;
    }
    100% {
      opacity: 1;
    }
  }
  img {
    width: 100%;
    height: 350px;
    border-radius:  15px 15px 0 0;
  }
  figcaption {
    text-align: center;
    padding: 15px;
    text-decoration: none;
    color: #FFF;
    background-color: #a8a9e7;
    border-radius: 0 0 15px 15px;
  }
}

.container-getProject {
  font-family: 'old-standard-regular';
  background: #FFF;
  padding: 25px 0;
  text-align: start;
  .back-dashboard {
    position: fixed;
    .box-2 {
      // height: 33.33%;
      width: 100%; 
      display: flex;
      justify-content: center;
      align-items: center;
    }
    
    .box-2 { background-color: transparent; }
    
    .btn {
      line-height: 39px;
      height: 35px;
      text-align: center;
      width: 100px;
      margin: 40px 5px 0 30px;
      cursor: pointer;
    }
    
    .btn-two {
      color: #021b39;
      transition: all 0.5s;
      position: relative;
    }
    .btn-two span {
      font-family: 'old-standard-bold';
      font-size: 9px;
      z-index: 2;	
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;	
    }
    .btn-two::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      transition: all 0.5s;
      border: 1px solid #021b394d;
      background-color: rgba(68, 32, 228, 0.1);
      border-radius: 15px;
    }
    .btn-two::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      transition: all 0.5s;
      border: 1px solid rgba(68, 32, 228, 0.2);
      background-color: rgba(68, 32, 228, 0.1);
      border-radius: 15px;
    }
    .btn-two:hover::before {
      transform: rotate(-45deg);
      background-color: rgba(255,255,255,0);
    }
    .btn-two:hover::after {
      transform: rotate(45deg);
      background-color: rgba(255,255,255,0);
    }
    

  }
  
  .container-project {
    padding: 1% 10% 1% 3%;
    margin: 3% 25% 3% 25%;
    background-color: #edecec77;
    text-align: start;
    border-radius: 15px;
    box-shadow: 5px 5px 5px #ebebf6;
    figure {
      margin: 0;
      // text-align: center;
      div {
        display: flex;
        margin: 30px 0;
        justify-content: space-between;
        h3 {
          align-self: center;
          margin: 0 10px 0 0;
          text-align: start;
          font-size: 20px;        }
      }
      h4, h5, figcaption {
        text-align: start;
        margin: 15px 0;
      }
      img {
        width: auto;
        height: 150px;
      }
      a {
        text-decoration: none;
        color: #000000;
        svg {
          margin: 0 5px
        }
      }
      figcaption {
        font-style: italic;
      }
    }
  }
  .container-project:hover {
    transform: scale(1.02);
        transition: all 1200ms;
        filter:none;
  }
}


@media only screen and (max-width: 870px) and (min-width: 575px) {
  .my-stack-container {
    margin: 1em !important;
  }
  .project-list {
    gap: 30px;
    img {
      height: 250px;
    }
  }
}

@media only screen  and (max-width: 575.98px) {
  .header {
    display: block;
    .about-me-container {
      margin: 2em;
    }
    .my-stack-container {
      grid-template-columns: repeat(2, 120px);
      grid-template-rows: repeat(6, 150px);
      gap: 10pxpx;
      margin: 1em auto;
      .my-stack svg {
        width: 30%;
      }
    }
  }
  .project-list {
    grid-template-columns: 1fr;
    margin: 0 2em;
    img {
      height: 180px;
    }
  }
  .container-getProject {
    .back-dashboard {
      position: absolute;
    }
    .container-project {
      display: block;
      margin: 25% 5% 3% 5%;
      figure div {
        display: block;
        h3 {
          margin-bottom: 5px;
        }
      }
    }
  } 
}